html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(1.15rem + 0.3vw);
}

p {
  font-size: calc(1.25rem + 0.5vw);
}

h1 {
  font-size: calc(1.5rem + 1.5vw);
}

h2 {
  font-size: calc(1.5rem + 1.25vw);
  margin-bottom: 2rem;
}

h3 {
  margin-bottom: 2rem;
  font-size: calc(1.5rem + 1vw);
}

a {
  text-decoration: none;
  color: #212121;
}

button {
  background-color: transparent;
  padding: 0.5rem 1rem;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

footer {
  margin-top: auto;
  text-align: center;
  padding: 1rem auto;
}

.directory-info {
  padding: 5rem 12.5vw;
}
.directory-info h3 {
  font-weight: bold;
}
.directory-info img {
  height: 3rem;
}
.directory-info p:first-of-type {
  margin-bottom: 5rem;
}
.directory-info .accordion {
  margin-bottom: 5rem;
}
.directory-info .accordion .element-info {
  display: flex;
  width: 90%;
  font-weight: bold;
  font-size: calc(1.25rem + 0.5vw);
}
.directory-info .accordion .element-info .name {
  width: 70%;
}
.directory-info .accordion .accordion-body {
  font-size: calc(1rem + 0.5vw);
}

.accordion-item {
  border: none;
}

/* Remove blue background and border on active accordion */
.accordion-button:not(.collapsed) {
  background-color: transparent !important;
  /* Removes blue background */
  color: inherit !important;
  /* Keeps text color default */
  border-color: transparent !important;
  /* Removes blue border */
}

/* Remove blue outline when clicking */
.accordion-button:focus {
  box-shadow: none !important;
  border-color: transparent !important;
}

/* Remove blue background from collapsed button */
.accordion-button {
  background-color: transparent !important;
  color: inherit !important;
}

.packages {
  background-color: #91D3EE;
  padding: 5rem 12.5vw;
}
.packages h3 {
  font-weight: bold;
}
.packages img {
  height: 3rem;
}
.packages .grid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
}
.packages .grid .package {
  width: 90%;
  margin-top: 3rem;
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.packages .grid .package h4 {
  background-color: #ffffff;
  color: #212121;
  width: fit-content;
  margin: auto;
  padding: 1rem 2rem;
}
.packages .grid .package .content {
  padding: 15%;
  background-color: #da779b;
  border-left: 10px solid #D25B86;
  border-right: 10px solid #D25B86;
}
.packages .grid .package .content ul {
  list-style: none;
  padding-left: 0;
}
.packages .grid .package .content li {
  background: url("../img/check.png") no-repeat left center;
  background-size: 2rem 1.5rem;
  padding-left: 24px;
  /* Adjust spacing */
}
.packages .grid .package .content .price {
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 4rem auto;
  margin-top: 0;
}
.packages .grid .package .content ul {
  color: #ffffff;
  margin-bottom: 4rem;
}
.packages .grid .package .content a {
  display: block;
  width: fit-content;
  margin: auto;
  margin-top: 4rem;
  color: #212121;
  background-color: #ffffff;
  padding: 0.5rem 2rem;
  transition: all 0.5s;
}
.packages .grid .package .content a:hover {
  transition: all 0.5s;
  background-color: gainsboro;
}
@media (min-width: 576px) {
  .packages .grid .package {
    width: 40%;
  }
}
@media (min-width: 992px) {
  .packages .grid .package {
    width: 30%;
  }
}

