@use "base/variables" as v;
@use "base/mixins" as m;

.directory-info {
    h3 {
        font-weight: bold;
    }

    padding: 5rem 12.5vw;

    img {
        height: 3rem;
    }

    p {
        &:first-of-type {
            margin-bottom: 5rem;
        }
    }

    .accordion {
        margin-bottom: 5rem;

        .element-info {
            display: flex;
            width: 90%;
            font-weight: bold;
            font-size: calc(1.25rem + 0.5vw);

            .name {
                width: 70%;
            }
        }
        .accordion-body {
            font-size: calc(1rem + 0.5vw);
        }
    }
}

.accordion-item {
    border: none;
}

/* Remove blue background and border on active accordion */
.accordion-button:not(.collapsed) {
    background-color: transparent !important;
    /* Removes blue background */
    color: inherit !important;
    /* Keeps text color default */
    border-color: transparent !important;
    /* Removes blue border */
}

/* Remove blue outline when clicking */
.accordion-button:focus {
    box-shadow: none !important;
    border-color: transparent !important;
}

/* Remove blue background from collapsed button */
.accordion-button {
    background-color: transparent !important;
    color: inherit !important;
}

.packages {
    background-color: v.$blue;

    h3 {
        font-weight: bold;
    }

    padding: 5rem 12.5vw;

    img {
        height: 3rem;
    }

    .grid {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        gap: 2rem;

        .package {
            width: 90%;
            margin-top: 3rem;
            margin-top: 3rem;
            display: flex;
            flex-direction: column;
            align-items: center;

            h4 {
                background-color: v.$white;
                color: v.$black;
                width: fit-content;
                margin: auto;
                padding: 1rem 2rem;
            }

            .content {
                padding: 15%;
                background-color: lighten(v.$pink, 7%);
                border-left: 10px solid v.$pink;
                border-right: 10px solid v.$pink;

                ul {
                    list-style: none;
                    padding-left: 0;
                }

                li {
                    background: url('../img/check.png') no-repeat left center;
                    background-size: 2rem 1.5rem;
                    padding-left: 24px;
                    /* Adjust spacing */
                }

                .price {
                    color: v.$white;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin: 4rem auto;
                    margin-top: 0;
                }

                ul {
                    color: v.$white;
                    margin-bottom: 4rem;
                }

                a {
                    display: block;
                    width: fit-content;
                    margin: auto;
                    margin-top: 4rem;
                    color: v.$black;
                    background-color: v.$white;
                    padding: 0.5rem 2rem;
                    transition: all 0.5s;
                    &:hover {
                        transition: all 0.5s;
                        background-color: darken(v.$gray, 10%);
                    }
                }
            }

            @include m.phone {
                width: 40%;
            }

            @include m.desktop {
                width: 30%;
            }
        }
    }
}