@use "base/variables" as v;
@use "base/mixins" as m;

nav {
    background-image: url("../img/bg.png");
    background-repeat: no-repeat;
    background-position: top center;
    background-size: 100rem auto;
    padding: 1rem 2.5rem !important;

    @include m.desktop {
        background-size: 200rem auto;
        padding: 1rem 5vw !important;
    }

    @include m.desktop {
        padding: 1rem 10vw !important;
    }

    .navbar-collapse {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 2rem;
        padding-bottom: 2rem;

        .navbar-nav {
            margin-bottom: 2rem !important;
        }

        .nav-link {
            width: 100%;
            text-align: center;
        }

        @include m.desktop {
            flex-direction: row;
            align-items: center;
            padding-top: 2rem;
            padding-bottom: 2rem;

            .navbar-nav {
                margin-bottom: 0 !important;
            }

            .nav-link {
                width: 100%;
                text-align: center;
            }
        }
    }

    .navbar-brand {
        height: 5rem;

        img {
            width: 100%;
            height: 100%;
        }
    }

    ul {
        margin: 0;
        width: 100%;
        justify-content: space-around;
        padding-right: 5rem !important;
        padding-left: 5rem !important;

        li {
            a {
                color: v.$white !important;
                width: 15rem;
                text-transform: uppercase;
                transition: all 0.75s;

                &:hover {
                    transition: all 0.25s;
                    transform: scale(1.1);
                }
            }

            .active {
                font-weight: bold;
            }
        }
    }

    button {
        background-color: v.$white;
        border: none;
        padding-left: 2rem;
        padding-right: 2rem;
        padding-top: 0.75rem;
        padding-bottom: 0.75rem;
        white-space: nowrap;
        text-transform: uppercase;
        transition: all 0.5s;
        border-radius: 0.5rem;

        &:hover {
            background-color: v.$black;
            color: v.$white;
            transition: all 0.5s;
        }
    }

    .navbar-toggler {
        border-color: v.$white;
        background-color: v.$white;
        padding-top: 0.75rem;
        padding-bottom: 0.75rem;
        border-width: 1px;

        &:focus {
            border-color: v.$white;
        }

        &:hover {
            background-color: darken(v.$gray, 10%);
        }

        &:focus {
            box-shadow: none;
        }
    }
}

.mision {
    background-color: v.$blue;
    text-align: center;
    padding: 2rem 10vw;

    p {
        margin-bottom: 2rem;
    }

    @include m.tablet {
        background-image: url("../img/bg1.webp");
        background-repeat: no-repeat;
        background-position: top center;
        background-size: cover;

        .right {
            width: 70%;
            margin: auto;
            padding: 15rem 0;
            margin-right: 0;

            p {
                margin-bottom: 4rem;
            }
        }
    }

    @include m.desktop {
        .right {
            width: 50%;
        }
    }

    @include m.desktopXL {
        .right {
            width: 40%;
            margin-right: 10vw;
            padding: 20rem 0;
        }
    }
}

.text-1 {
    padding: 2rem 10vw;
    padding-bottom: 4rem;
    display: flex;
    flex-direction: column;
    align-items: center;

    span {
        font-weight: bold;
    }

    p {
        margin-bottom: 2rem;
        text-align: center;
    }

    img {
        width: 50vw;
        margin: auto;
    }

    @include m.tablet {
        padding: 10rem 15vw;

        img {
            width: 30vw;
        }

        p {
            margin-bottom: 4rem;
        }
    }

    @include m.desktop {
        padding: 10rem 30vw;

        img {
            width: 30rem;
        }
    }
}

.text-2 {
    background-color: v.$blue;
    display: flex;
    flex-direction: column;
    padding: 2rem 10vw;
    padding-bottom: 5rem;

    .text-left {
        text-align: center;
        margin-bottom: 4rem;
    }

    .img-right {
        img {
            width: 100%;
        }
    }

    p {
        margin-bottom: 2rem;
    }


    @include m.phone {
        .text-left {
            text-align: left;
            padding-right: 5rem;
        }

        padding: 10rem 15vw;
        flex-direction: row;

        p {
            margin-bottom: 4rem;
        }
    }

    @include m.desktop {
        .text-left {
            padding-right: 7.5rem;
        }

        padding: 10rem 10vw;
    }

}

.services {
    padding: 5rem 10vw;

    h3 {
        text-align: center;
        margin-bottom: 2rem;
    }

    p {
        text-align: center;
    }

    .services-grid {
        margin: 2rem auto;
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;

        @include m.phoneSM {
            grid-template-columns: 1fr 1fr;
        }

        @include m.tablet {
            grid-template-columns: 1fr 1fr 1fr;
            margin-top: 5rem;
        }

        .service-grid {
            // max-width: 20vw;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            background-color: v.$pink;
            border-radius: 1rem;
            height: 100%;

            img {
                width: auto;
                height: 5rem;
                margin-top: 2rem;
                margin-bottom: 1rem;
            }

            p {
                margin: 0 2rem 2rem;
                color: v.$white;
                margin-bottom: 2rem;
            }

            @include m.phoneSM {
                width: 100%;
                margin: auto;
            }

            @include m.phone {
                width: 80%;
                margin: auto;
            }

            @include m.desktop {
                max-width: 30rem;
                margin: auto;
            }
        }

        @include m.desktop {
            margin-right: 10vw;
            margin-left: 10vw;
        }
    }
}

.model {
    padding: 5rem 12.5vw;
    padding-top: 0;

    h3,
    p {
        text-align: center;
    }

    img {
        display: none;
    }

    .steps {
        display: grid;
    }

    .number {
        margin: 4rem auto;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 8rem;
        height: 8rem;
        border-radius: 10rem;
        color: white;
        font-weight: bold;
        font-size: calc(2rem + 1vw);
        background: linear-gradient(to bottom right, v.$pink, v.$blue);
    }

    .step {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    @include m.phone {
        .steps {
            img {
                display: block;
                width: 105%;
                height: 7rem;
            }

            grid-template-columns: repeat(2, 1fr);
            gap: 2rem;

            .text {
                height: 27rem;
            }

            .step-uneven {
                margin-top: 5rem;
                .number {
                    position: relative;
                    top: -10rem;
                    z-index: 3;
                }

                .text {
                    order: -2;
                }

                img {
                    position: relative;
                    top: -5.6rem;
                }

                justify-content: start;
            }

            .step-even {
                margin-bottom: 5rem;
                justify-content: end;

                .number {
                    order: -1;
                    position: relative;
                    top: 10rem;
                    z-index: 3;
                }

                img {
                    transform: rotateX(180deg);
                    position: relative;
                    top: 5.6rem;
                }

                .text {
                    display: flex;
                    flex-direction: column;
                    justify-content: end;
                }
            }
        }
    }

    @include m.tablet {
        padding: 7.5rem 12.5vw;
        .steps {
            margin: 10rem auto;
            grid-template-columns: repeat(3, 1fr);

            .text {
                height: 35rem;
            }

            .step-uneven {
                .number {
                    top: -15rem;
                }

                img {
                    top: -9.6rem;
                }

                justify-content: start;
            }

            .step-even {
                .number {
                    top: 15rem;
                }

                img {
                    top: 9.6rem;
                }
            }
        }

    }
}

.process {
    padding: 5rem 12.5vw;
    padding-top: 0;
    h3,p {
        text-align: center;
    }
    .steps {
        margin: 4rem auto;
        .step {
            margin: 3rem auto;
            display: flex;
            flex-direction: column;
            justify-content: start;
            align-items: center;
            .number {
                width: 10rem;
                height: 10rem;
                background-color: v.$blue;
                display: flex;
                justify-content: center;
                align-items: center;
                p {
                    margin: 0;
                    color: v.$white;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    background-color: v.$blue;
                    width: 9.25rem;
                    height: 9.25rem;
                    border: 2px solid v.$white;
                    font-size: calc(3rem + 1vw);
                }
                
            }
            h4,p {
                text-align: center;
            }
            h4 {
                font-weight: bold;
                margin-top: 2rem;
            }
        }
        @include m.phone {

            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            gap: 2rem;
            .step {
                width: 40%;
                margin: 1rem auto;
            }
        }
        @include m.desktop {
            .step {
                width: 30%;
            }
        }
        @include m.desktopXL {
            .step {
                width: 20%;
                margin: 2rem auto;
            }
        }
    }
}

.index-last {
    padding: 5rem 12.5vw;
    padding-top: 0;

    h3 {
        text-align: center;
    }

    .index-last-grids {
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;
        margin-top: 6rem;

        @include m.phone {
            grid-template-columns: 1fr 1fr;
        }

        @include m.tablet {
            grid-template-columns: 1fr 1fr 1fr;
            margin-top: 5rem;
        }

        .index-last-grid {
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding: 3rem;
            box-shadow: 0px 0px 22px -1px rgba(0, 0, 0, 0.75);
            -webkit-box-shadow: 0px 0px 22px -1px rgba(0, 0, 0, 0.75);
            -moz-box-shadow: 0px 0px 22px -1px rgba(0, 0, 0, 0.75);

            h4 {
                text-align: center;
            }
        }
    }
}

.btn-1 {
    border-radius: 10rem;
    padding: 0.5rem 1.5rem;
    border: 1px solid v.$pink;
    background-color: transparent;
    transition: all 0.25s;
    text-align: center;
    margin: auto;
    display: flex;
    justify-content: center;
    width: fit-content;

    &:hover {
        background-color: v.$pink;
        color: v.$white;
        transition: all 0.25s;
    }
}