@use "base/variables" as v;
@use "base/mixins" as m;

nav a {
    font-size: calc(1rem + 0.4vw) !important;
}

h3 {
    font-size: calc(1.75rem + 0.5vw);
}

h4 {
    font-size: calc(1.25rem + 0.5vw);
}

p {
    margin-top: 2rem;
}

li,
p {
    font-size: calc(1.25rem + 0.5vw);
}

.info {

    h3 {
        font-weight: bold;
    }

    h3 {
        font-size: calc(1.75rem + 0.5vw);
    }

    h4 {
        font-size: calc(1.5rem + 0.5vw);
    }

    p {
        margin-top: 2rem;
    }

    li,
    p {
        font-size: calc(1.25rem + 0.5vw);
    }

    padding: 5rem 12.5vw;

    img {
        height: 3rem;
    }

    .title-img {
        margin-bottom: 5rem;
    }

    p {
        text-align: justify;
    }

    ul {
        li {
            list-style: disc;
        }
    }

    ol {
        &>li:first-child {
            margin-top: 1rem;
        }

        &>li:last-child {
            margin-bottom: 2rem;
        }
    }

    span {
        font-weight: bold;
    }

    @include m.tablet {
        margin-top: 5rem;
    }
}

.book {
    margin-top: 10rem;
    border: 10vw solid v.$gray;
    padding: 5rem 10vw;

    h3,
    p {
        text-align: center;
    }

    a {
        margin-top: 5rem;
    }

    @include m.tablet {
        border: 10rem solid v.$gray;
        padding: 5rem 12.5vw;
    }
}

.general-info1 {
    background-color: v.$blue;
    display: flex;
    flex-direction: column;
    align-items: center;

    .general-info1-text {
        h3 {
            text-align: center;
        }

        p {
            text-align: justify;
        }

        margin: 10rem 12.5vw;
        margin-bottom: 5rem;
        padding: 5rem 5vw;
        background-color: lighten(v.$blue, 10%);
    }

    a {
        margin-bottom: 10rem;
    }
}

.general-services1 {
    background-color: v.$blue;
    padding: 5rem 0;

    h3 {
        font-size: calc(1.75rem + 0.5vw);
    }

    h4 {
        font-size: calc(1.5rem + 0.5vw);
    }

    p {
        margin-top: 2rem;
    }

    li,
    p {
        font-size: calc(1.25rem + 0.4vw);
    }

    h3 {
        text-align: center;
    }

    .grid {
        display: grid;
        grid-template-columns: 1fr;
        gap: 5rem;
        margin: 5rem 12.5vw;

        @include m.tablet {
            grid-template-columns: 1fr 1fr;
            gap: 6rem;
        }

        @include m.desktop {
            grid-template-columns: 1fr 1fr 1fr;
            gap: 3rem;
        }

        .element {
            background-color: lighten(v.$blue, 10%);
            padding: 2rem 3rem;
            display: flex;
            flex-direction: column;
            align-items: center;

            img {
                width: 5rem;
                margin: 2rem;
            }

            p {
                text-align: justify;
                margin-top: 1rem;
            }
        }
    }
}

.general-characteristics1 {
    background-color: v.$blue;
    padding: 5rem 12vw;

    h3 {
        text-align: center;
    }

    .items {
        display: grid;

        @include m.phoneSM {
            grid-template-columns: repeat(2, 1fr);
            gap: 5rem;
        }

        @include m.tablet {
            grid-template-columns: repeat(4, 1fr);
            gap: 5rem;
        }

        .item {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin: 3rem auto;

            h4 {
                margin-top: 2rem;
                text-align: center;
            }

            .item-img {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 10rem;
                height: 10rem;
                padding: 0.5rem;
                border-radius: 10rem;
                background-color: darken(v.$blue, 10%);

                img {
                    width: 100%;
                }
            }
        }
    }

    a {
        margin-top: 4rem;
    }

    @include m.desktop {
        padding-left: 15%;
        padding-right: 15%;
    }

    @include m.desktopXL {
        padding-left: 25%;
        padding-right: 25%;
    }
}

.no-interaction {
    pointer-events: none;
}

.no-interaction a,
.no-interaction p,
.no-interaction h1,
.no-interaction h2,
.no-interaction h3,
.no-interaction h4,
.no-interaction .footer-content-links .footer-title,
.no-interaction .footer-info {
    pointer-events: auto;
}