@use "base/variables" as v;
@use "base/mixins" as m;

.us {
    h3 {
        font-weight: bold;
    }

    padding: 5rem 12.5vw;

    img {
        height: 3rem;
    }

    p {
        &:first-of-type {
            margin-bottom: 5rem;
        }
    }
}

.text-3 {
    background-color: v.$blue;
    display: flex;
    flex-direction: column;
    padding: 2rem 10vw;
    padding-bottom: 5rem;

    .text-left {
        text-align: center;
        margin-bottom: 4rem;

        p {
            text-align: justify;
        }
    }

    .img-right {
        img {
            width: 100%;
            padding: 4rem;

            @include m.phone {
                padding: 0;
            }
        }
    }

    p {
        margin-bottom: 2rem;
    }


    @include m.phone {
        .text-left {
            text-align: left;
            padding-left: 5rem;
        }

        padding: 10rem 15vw;
        flex-direction: row;

        p {
            margin-bottom: 4rem;
        }
    }

    @include m.desktop {
        .text-left {
            padding-left: 7.5rem;
        }

        padding: 10rem 15vw;
    }

}

.text-4 {
    padding: 5rem 12.5vw;

    p {
        text-align: center;
        margin: 0;
    }

    span {
        font-weight: bold;
    }

    .index-last-grids {
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;
        margin-top: 2rem;
        margin-bottom: 2rem;

        @include m.phone {
            grid-template-columns: 1fr 1fr;
        }

        @include m.tablet {
            grid-template-columns: 1fr 1fr 1fr;
            // margin-top: 5rem;
        }

        @include m.desktop {
            margin-top: 6rem;
            margin-bottom: 6rem;
        }

        .index-last-grid {
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding: 3rem;
            border: 4px solid v.$blue;

            h4 {
                text-align: center;
            }
        }
    }

    @include m.desktop {
        padding: 5rem 20vw;
    }
}

.text-5 {
    background: linear-gradient(#99023960, #99023960),
        url('../img/bg3.webp');
    background-size: cover;
    padding: 5rem 12.5vw;

    span {
        font-weight: bold;
    }

    p {
        text-align: center;

        &:first-of-type {
            margin-bottom: 5rem;
        }
    }

    @include m.desktop {
        padding: 5rem 20vw;
    }
}

.blue-bg {
    background-color: v.$blue;
    .button {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-bottom: 5rem;
    }
}

.text-6 {
    padding: 5rem 12.5vw;

    p {
        text-align: center;
    }

    .stats {
        margin-top: 4rem;
    }

    .stats i {
        width: 54px;
        height: 54px;
        font-size: 24px;
        border-radius: 50px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        position: relative;
        z-index: 1;
    }

    .stats .stats-item {
        margin-top: -27px;
        padding: 30px 30px 25px 30px;
        width: 100%;
        position: relative;
        text-align: center;
        border-radius: 4px;
        z-index: 0;
    }

    .stats .stats-item span {
        font-size: 36px;
        display: block;
        font-weight: 700;
    }

    .stats .stats-item p {
        padding: 0;
        margin: 0;
        font-family: var(--heading-font);
        font-size: 16px;
    }

    @include m.desktop {
        padding: 5rem 20vw;
    }
}

.gallery {
    padding: 0 12.5vw;
    padding-bottom: 5rem;
    display: grid;
    grid-auto-rows: auto;
    grid-auto-flow: dense;
    gap: 6px;
    grid-template-columns: repeat(2, 1fr);

    img.one-grid-cell {
        grid-column: 1 / 3;
        // grid-row: 3 / 4;
    }

    @include m.tablet {
        grid-template-columns: repeat(4, 1fr);

        img.two-grid-cells {
            grid-column: 1 / 2;
            grid-row: 1 / 3;
        }

        img.one-grid-cell {
            grid-column: 1 / 2;
            grid-row: 3 / 4;
        }

        img.two-grid-cells-2 {
            grid-column: 2 / 3;
            grid-row: 2 / 4;
        }

        img.four-grid-cells {
            grid-row: 2 / 4;
            grid-column: 3 / 5;
        }
    }

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border: 4px solid v.$white;
    }

    @include m.desktop {
        padding: 0 15vw;
        padding-bottom: 5rem;
    }
}