@use "base/variables" as v;
@use "base/mixins" as m;

.specialties-header {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
        url('../img/hero1.png');
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    content-visibility: auto;

    nav {
        background-image: unset;
        // background: content-box
        background-color: transparent !important;
    }

    .hero {
        padding-left: 10vw;
        padding-right: 10vw;
        height: 30rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        h3,
        p {
            color: v.$white;
            text-align: center;
        }

        @include m.desktop {
            height: 50rem;
        }
    }
}

.specialties {
    padding: 2rem 10vw;
    margin-top: 2rem;

    h3,
    p {
        text-align: center;
    }

    .main {
        margin: 3rem auto;

        .grid {
            display: grid;
            gap: 2rem;

            .element {
                background-color: v.$blue;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;

                h4 {
                    color: v.$white;
                    padding: 2rem;
                    text-align: center;
                }

                a {
                    background-color: v.$white;
                    color: v.$black;
                    padding: 0.5rem 2rem;
                    margin-bottom: 2rem;
                    transition: all 0.5s;

                    &:hover {
                        background-color: darken(v.$gray, 10%);
                        transition: all 0.5s;
                    }
                }
            }

            @include m.phoneSM {
                max-width: 25rem;
                margin: auto;
            }

            @include m.phone {
                max-width: 55rem;
                grid-template-columns: repeat(2, 1fr);
            }

            @include m.tablet {
                grid-template-columns: repeat(3, 1fr);
                margin-left: 0;

                h4 {
                    height: 7rem;
                }
            }
        }
    }

    img {
        display: none;
    }

    @include m.desktop {
        img {
            display: block;
            height: 40rem;
            position: absolute;
            right: 12vw;
            top: calc(30rem + 58rem);
        }
        margin-top: 8rem;
    }

    @include m.desktopXL {
        .main {
            margin-left: 5vw;

            .element {
                width: 15vw;
            }
        }

        img {
            height: 45rem;
            right: 12%;
            top: calc(27.5rem + 58rem);
        }
    }
}