@use "base/variables" as v;
@use "base/mixins" as m;

$contentPaddingVertical: 2.5rem;
$contentPaddingHorizontal: 2rem;

.footer-3 {

    z-index: -10;

    top: 0;

    @include m.tablet {
        position: relative;
        top: -20vw !important;
    }

    @include m.desktop {
        position: relative;
        top: -15vw !important;
    }

    @include m.desktopXL {
        position: relative;
        top: -10vw !important;
    }

    .footer-img {
        width: 80%;
        margin: auto;
        position: relative;
        top: 20rem;

        img {
            width: 100%;
        }

        display: none;

        @include m.tablet {
            display: block;
        }

        .footer-img-text {
            h3 {
                font-size: calc(0.5rem + 1.75vw);
            }

            p {
                font-size: calc(0.5rem + 1.75vw);
                margin-bottom: calc(2rem + 0.5vw);
            }

            position: relative;
            top: -16vw;
            right: -15vw;
            margin: auto;
            color: v.$white;
            width: 50%;

            .social {
                display: flex;
                justify-content: center;

                a {
                    width: 2rem;
                    background-color: v.$white;
                    margin: 1rem;
                    width: calc(3rem + 1.75vw);
                    height: calc(3rem + 1.75vw);
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border-radius: 10rem;
                    transition: all 0.25s;

                    &:hover {
                        transition: all 0.25s;
                        background-color: darken(v.$gray, 10%);
                    }

                    svg {
                        width: calc(1rem + 1.75vw);
                        height: calc(1rem + 1.75vw);
                    }
                }
            }
        }

        @include m.desktop {
            top: 27.5rem;
        }
    }

    .footer-content {

        background-color: v.$blue;
        color: v.$black;
        padding-top: $contentPaddingVertical;
        padding-bottom: 2rem;

        &>*:not(:first-child) {
            padding-top: 2rem;
        }

        .footer-title {
            text-transform: uppercase;
            padding-bottom: 1rem;
            font-weight: 500;

            img {
                width: 50vw;
                margin-bottom: 2rem;

                @include m.phone {
                    width: 20rem;
                }
            }
        }

        .footer-content-description {
            .footer-info {
                margin-bottom: 3rem;
            }
        }

        .footer-info {
            margin: auto 15vw;
            @include m.desktop {
                margin: auto 5vw
            }
        }

        .links-2 {
            margin: 4rem 10vw;
            @include m.phone {
                margin-top: 0;
            }
            @include m.desktop {
                margin-right: 0;
            }
        }

        @include m.phoneSM {
            padding-left: 5rem;
            padding-right: 5rem;
        }

        @include m.phone {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            padding-left: 10vw;
            padding-right: 10vw;
            padding-top: 5rem;
            padding-bottom: 10rem;

            .footer-content-description {
                grid-area: 1 / 1 / 2 / 3;
                padding-bottom: 1rem;
            }
        }

        @include m.tablet {
            padding-top: 10rem;
        }

        @include m.desktop {
            padding-bottom: 10rem;
            padding-top: 20rem;
            display: flex;
            padding-left: 5vw;
            padding-right: 5vw;
            justify-content: space-around;

            .footer-content-description {
                display: flex;
                flex-direction: column;
                align-items: baseline;
                width: 30%;

                .footer-info {
                    text-align: left;
                }
            }

            &>*:not(:first-child) {
                padding-top: 0;
                padding-left: 4rem;
            }

            .footer-content-links {
                width: 30%;
                display: flex;
                flex-direction: column;
                align-items: baseline;
            }
        }

        .footer-content-links {
            z-index: 3  ;
            .footer-link {
                display: flex;
                justify-content: center;
                align-items: start;
                margin: 0.5rem 0;

                span {
                    padding-top: 0.25rem;
                    padding-right: 1rem;
                }

                a {
                    color: v.$black;
                    text-align: start;
                    transition: all 0.25s;
                    text-align: center;

                    &:hover {
                        color: v.$white;
                        transition: all 0.25s;
                    }

                    @include m.desktop {
                        text-align: left;
                    }
                }

                .material-symbols-outlined {
                    display: none;

                    @include m.desktop {
                        display: block;
                    }
                }
            }
        }

        @include m.desktop {
            .links-1 {
                width: 20%;
            }

            .links-2 {
                width: 40%;
                padding-left: 0;
                padding-right: 2rem;
            }
        }
    }

    .footer-register {
        padding-top: 2rem;
        padding-bottom: 2rem;
        border-bottom: 1px solid v.$black;
        border-top: 1px solid v.$black;
        margin-right: 1.5rem;
        margin-left: 1.5rem;

        &>* {
            &:first-child {
                padding-bottom: 1rem;
            }
        }

        @include m.phoneSM {
            display: flex;
            justify-content: center;
            align-items: center;

            &>* {
                &:first-child {
                    padding-bottom: 0;
                    padding-right: 1.5rem;
                }
            }
        }

        .footer-register-btn {
            display: inline-block;
            border: 1px solid v.$white;
            padding: 0.5rem 2rem;
            border-radius: 2rem;

            a {
                color: v.$white;
            }

            &:hover {
                background-color: v.$black;
            }
        }
    }

    .footer-social {
        padding-bottom: $contentPaddingVertical;
        padding-top: 2rem;

        i {
            border: 1px solid v.$white;
            padding: 0.5rem;
            border-radius: 2rem;
            margin: 0.5rem;

            &:hover {
                background-color: v.$black;
            }

            a {
                color: v.$white;
            }
        }
    }

    &>*:not(.footer-copy) {
        padding-right: $contentPaddingHorizontal;
        padding-left: $contentPaddingHorizontal;

        @include m.desktop {
            padding-right: 5vw;
            padding-left: 5vw;
        }
    }

    .footer-copy {
        background-color: v.$white;
        padding: 1.5rem;
    }
}